.collection-list {
    // display: none;
    // padding: 0 3em;
    display: grid;
    grid-template: auto 1fr auto auto / repeat(3, auto);
    & h3 {
      grid-column: span 3;
    }
    // grid-gap: 4em 2em;
    // & h3 {
    //   justify-self: center;
    // }
    // & > div {
    //   justify-self: center;
    //   display: grid;
    //   grid-template-columns: repeat(3, minmax(auto, 350px));
    //   grid-template-rows: 550px auto;
    //   grid-column-gap: 1em;
    //   & > div {
    //     background-color: #EEE;
    //     transition: transform .2s;
    //     cursor: pointer;
    //     &:hover {
    //       transform: scale(1.05);
    //     }
    //   }
    //   & > span {
    //     justify-self: center;
    //   }
    // }
    & img {
      max-width: 100%;
    }
    & button {
      grid-column: span 3;
      padding: 0.5em 1em;
      border: none;
      color: #FFF;
      background-color: #000;
      justify-self: center;
      & a {
        text-decoration: none;
        color: #FFF;
      }
    }
}

@media (max-width: 768px) {
  .collection-list {
    grid-template: 1fr / 1fr;
  }
}